import React, { Component } from "react";
import { Grid, Row, Col, DropdownButton, Pagination } from "react-bootstrap";
import CustomButton from "components/CustomButton/CustomButton";
import TableList from "./TableList.jsx";

import { apiBaseURL } from "./Dashboard.jsx";

const axios = require("axios");

const frontendToBackend = {
  "Patient ID": "pt_id",
  "Image Info": "images",
  "Exam ID": "exam_id",
  "Exam Date": "exam_date"
};

//Feb. 10, 2020: ExamPage data is not displaying currently

//ExamPage displays all patient exams on a page
class ExamPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      patientsIDs: [],
      patientInfo: [],
      filterCategories: [
        "Patient ID",
        "Image Info",
        "Exam ID",
        "Exam Date",
        "Exam Links"
      ],
      selectedFilterCategories: [
        "Patient ID",
        "Image Info",
        "Exam ID",
        "Exam Date",
        "Exam Links"
      ],
      tableKey: 1,
      exportPressed: {
        display: false
      },
      paginationNumber: 1
    };
    this.getData = this.getData.bind(this);
    this.editData = this.editData.bind(this);
    this.getFilters = this.getFilters.bind(this);
    this.categoryFilterPressed = this.categoryFilterPressed.bind(this);
    this.backButtonPressed = this.backButtonPressed.bind(this);
    this.exportAllImagesPressed = this.exportAllImagesPressed.bind(this);
    this.getExport = this.getExport.bind(this);
    this.exportCategoryPressed = this.exportCategoryPressed.bind(this);
    this.exportImagesPressed = this.exportImagesPressed.bind(this);
    this.paginationClicked = this.paginationClicked.bind(this);
    this.getPagination = this.getPagination.bind(this);
  }

  componentDidMount() {
    if (!this.props.pageStatus.ExamsPage) {
      return
    }

    this.setState(
      {
        patientsIDs: this.props.pageStatus.ExamsPage.patientsIDs
      },
      () => this.getData()
    );
  }

  //Similar to the other pages, getData() acquires data from the API and sets the state of patientInfo to currentInfo
  getData() {
    var currentComponent = this;
    for (var i = 0; i < this.state.patientsIDs.length; i++) {
      var patientID = this.state.patientsIDs[i];
      var link = apiBaseURL + "/ssd_api/patient_images?pt_id=" + patientID;
      var autToken = "Basic " + btoa(this.props.accessToken + ":something")

      const options = {
        url: link,
        method: 'get',
        headers: {
          'Authorization': autToken
        },
      };

      axios(options)
        .then(function (response) {
          let currentInfo = currentComponent.state.patientInfo;
          currentInfo.push(response.data.result);
          currentComponent.setState({ patientInfo: currentInfo }, () => {
            currentComponent.editData();
          });
        })
        .catch(function (error) {
          console.log(error);
          if (error.message === "Request failed with status code 401") {
            currentComponent.props.backToLoginPage()
          }
        });
    }
  }

  //gets filter names from this.state.filterCategories (already preset to certain ones (see top of code))
  getFilters() {
    var filter_categories = this.state.filterCategories;
    var temp_filter_categories = [];
    for (var i = 0; i < filter_categories.length; i++) {
      var category_name = filter_categories[i];
      var temp_filter_category = null;
      if (this.state.selectedFilterCategories.indexOf(category_name) !== -1) {
        //if category_name exists in selectedFilterCategories...
        temp_filter_category = (
          <CustomButton
            style={styles.buttonDivPressed}
            title={category_name}
            onClick={e => this.categoryFilterPressed(e)}
          >
            {" "}
            {category_name}{" "}
          </CustomButton>
        );
      } else {
        temp_filter_category = (
          <CustomButton
            style={styles.buttonDiv}
            title={category_name}
            onClick={e => this.categoryFilterPressed(e)}
          >
            {" "}
            {category_name}{" "}
          </CustomButton>
        );
      }
      temp_filter_categories.push(temp_filter_category);
    }
    return temp_filter_categories;
  }

  editData() {
    if (this.state.patientsIDs.length !== this.state.patientInfo.length) {
      return;
    }

    let patientInfo = {};

    for (var i = 0; i < this.state.patientInfo.length; i++) {
      let patient = this.state.patientInfo[i];
      let key_list = Object.keys(patient);
      patientInfo[key_list[0]] = patient[key_list[0]];
    }
    this.setState({
      patientInfo: patientInfo,
      loaded: "true"
    });
  }

  categoryFilterPressed(e) {
    let category = e.target.title;
    if (this.state.selectedFilterCategories.indexOf(category) === -1) {
      this.state.selectedFilterCategories.push(category);
      this.setState({
        selectedFilterCategories: this.state.selectedFilterCategories,
        tableKey: this.state.tableKey + 1
      });
    } else {
      var new_list = this.state.selectedFilterCategories.filter(function (name) {
        return name !== category;
      });
      this.setState({
        selectedFilterCategories: new_list,
        tableKey: this.state.tableKey + 1
      });
    }
  }

  //Three kinds of possible value to be generated
  //1. Patient ID
  //2. Images
  //3. actual exam information
  //returns TableList object with the categories as the columns and table information as the rows
  getTable() {

    var nullTable =
      <Col style={styles.titleText}>
        <div> No Exams Satisfies This Criteria </div>
      </Col>

    if (!this.state.loaded || !this.state.patientInfo) {
      return nullTable;

    }
    let patientInfo = this.state.patientInfo;
    let categoryTitles = [];
    let tableData = [];

    for (var i = 0; i < this.state.patientsIDs.length; i++) {
      let patientID = this.state.patientsIDs[i];
      for (var j = 0; j < patientInfo[patientID].length; j++) {
        let examInfo = patientInfo[patientID][j];
        let examID = [];
        let tempPatientInfo = [];
        for (var k = 0; k < this.state.filterCategories.length; k++) {
          var value = {}; // added this
          let category = this.state.filterCategories[k];
          if (this.state.selectedFilterCategories.indexOf(category) !== -1) {
            if (categoryTitles.indexOf(category) === -1) {
              categoryTitles.push(category);
            }
            if (category === "Patient ID") {
              value["type"] = "button";
              value["text"] = patientID;

              var tempPageStatus = this.props.pageStatus;
              tempPageStatus["PatientHistoryPage"] = {
                patientID: patientID,
                patientInfo: patientInfo[patientID]
              };
              let newState = {
                page: "PatientHistoryPage",
                pageStatus: tempPageStatus
              };
              value["submitFunction"] = newState =>
                this.props.changePage(newState);
              value["submitInformation"] = newState;

              tempPatientInfo.push(value);
            } else if (category === "Exam Links") {
              examID = examInfo["exam_id"];
              value["text"] = examID;
              value["type"] = "button";
              value["submitFunction"] = () => { }
              value["submitInformation"] = null;

              tempPatientInfo.push(value);
            } else if (category === "Image Info") {
              var text = [];
              var images = examInfo[frontendToBackend[category]];
              var lastImage = images[images.length - 1];

              if (images.length === 0) {
                value["type"] = "string";
                value["text"] = "";
                tempPatientInfo.push(value);
              } else {
                text.push(
                  "Image ID (First): " +
                  (lastImage.image_id - lastImage.image_num + 1)
                );
                text.push(<br />);
                text.push("# of Images: " + lastImage.image_num);
                text.push(<br />);
                text.push("Image Laterality: " + lastImage.image_laterality);
                text.push(<br />);
                text.push("Image Type: " + lastImage.image_type);

                value["type"] = "string";
                value["text"] = text;
                tempPatientInfo.push(value);
              }
            } else {
              //will need to come back to this part to populate the exams page table
              value["type"] = "string";
              value["text"] = examInfo[frontendToBackend[category]];
              tempPatientInfo.push(value);
              //tempPatientInfo.push(examInfo[frontendToBackend[category]])
            }
          }
        }
        tableData.push(tempPatientInfo);
      }
    }

    var tempTableData = []
    for (var i = (this.state.paginationNumber - 1) * 5; i < Math.min((this.state.paginationNumber) * 5,tableData.length); i++) {
      tempTableData.push(tableData[i])
    }
    this.state.tableKey++;

    return (
      <TableList
        key={this.state.tableKey}
        columns={categoryTitles}
        rows={tempTableData}
      ></TableList>
    );
  }

  backButtonPressed() {
    let newState = {
      page: "PatientsPage"
    }; /*let allows you to declare variables that are limited to a scope of ablock statement, unlike var, which defines variable globally*/
    this.props.changePage(newState);
  }

  exportAllImagesPressed() {
    this.setState({
      exportPressed: {
        display: !this.state.exportPressed.display
      }
    });
  }

  exportCategoryPressed(e) {
    let category = e.target.title;
    if (this.state.exportPressed[category]) {
      this.setState({
        exportPressed: {
          [category]: !this.state.exportPressed.display,
          display: true
        }
      });
    } else {
      this.setState({
        exportPressed: {
          [category]: true,
          display: true
        }
      });
    }
  }

  exportImagesPressed() {
    //code for exporting all images
  }

  getExport() {
    if (!this.state.exportPressed.display) {
      return (
        <CustomButton
          style={styles.buttonUpperExport}
          onClick={() => this.exportAllImagesPressed()}
        >
          EXPORT ALL IMAGES
        </CustomButton>
      );
    }
    return (
      <div>
        <CustomButton
          style={styles.buttonUpperExport}
          onClick={() => this.exportAllImagesPressed()}
        >
          EXPORT ALL IMAGES
        </CustomButton>
        <div style={styles.exportDropdownPressed}>
          <div>
            <input
              type="checkbox"
              title={"by Patient ID"}
              onChange={e => this.exportCategoryPressed(e)}
            />
            by Patient ID
          </div>
          <div>
            <input
              type="checkbox"
              title={"by diagnosis"}
              onChange={e => this.exportCategoryPressed(e)}
            />
            by diagnosis
          </div>
          <div>
            <input
              type="checkbox"
              title={"by image procedure"}
              onChange={e => this.exportCategoryPressed(e)}
            />
            by image procedure
          </div>
        </div>
        <CustomButton
          style={styles.buttonUpperExport}
          onClick={() => this.exportImagesPressed()}
        >
          EXPORT IMAGES
        </CustomButton>
      </div>
    );
  }

  paginationClicked(e) {
    this.setState({
      paginationNumber: e.target.title
    })
  }

  getPagination() {
    var pagination = []
    if (!this.state.patientInfo) {
      return null
    }
    for (var number = 0; number < Object.keys(this.state.patientInfo).length / 5; number++) {
      if (number+1 === this.state.paginationNumber) {
        pagination.push(
          <Pagination.Item title={number + 1} style={styles.paginationActive} onClick={e => this.paginationClicked(e)}>
            {number + 1}
          </Pagination.Item>,
        );
      }
      else {
      pagination.push(
        <Pagination.Item title={number + 1} onClick={e => this.paginationClicked(e)}>
          {number + 1}
        </Pagination.Item>,
      );
      }
    }
    return <Pagination style={styles.paginationStyle}>{pagination}</Pagination>
  }

  render() {
    var all_filters = this.getFilters();
    var exportButton = this.getExport();
    var mainTable = this.getTable();
    var pagination = this.getPagination();
    console.log("ExamsPage", this.state);
    return (
      <div>
        <Grid fluid>
          <Row style={styles.titleStyle}>
            <Col lg={12} sm={8} style={styles.titleText}>
              <div>Your Exams For Patients</div>
            </Col>
          </Row>
          <Row style={styles.summaryStyle}>
            <Col style={styles.summaryText}>
              <div style={styles.underMainStyle}>
                <DropdownButton style={styles.buttonContainer} title="TAKE AN ACTION">
                  <CustomButton
                    style={styles.buttonUpperBack}
                    onClick={() => this.backButtonPressed()}
                  >
                    BACK TO PATIENTS PAGE
                    </CustomButton>
                  {exportButton}
                </DropdownButton>
              </div>
            </Col>
          </Row>
          <Row>
            <Col sm={3}>
              <Row>
                <CustomButton style={styles.buttonDivShow}>
                  {" "}
                  Shown{" "}
                </CustomButton>
                <CustomButton style={styles.buttonDivHide}>
                  {" "}
                  Hidden{" "}
                </CustomButton>
              </Row>
              <Row>{all_filters}</Row>
            </Col>
            <Col sm={9}>
              <Grid fluid>
                <Row>
                  <div style={styles.mainDivStyle}>
                  {mainTable}
                  {pagination}
                  </div>
                </Row>
              </Grid>
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}
export default ExamPage;

